/**
* 双随机-检查计划组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from
                    :onCreate="onCreate"
                    :onDel="onDelAll"
                    :searchObj="searchObj"
                    @searchList="searchList"
            />
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                @selection-change="handleSelectionChange"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column align="center" label="对象名称" prop="objectName" show-overflow-tooltip/>

            <el-table-column align="center" label="对象类型" prop="objectType" show-overflow-tooltip>
                <template
                        slot-scope="scope"
                        v-if="scope.row.objectType"
                >{{scope.row.objectType | objectType}}
                </template>
            </el-table-column>
            <!-- <el-table-column align="center" label="检查状态" prop="objectName" show-overflow-tooltip/> -->
            <el-table-column align="center" label="检查单位" prop="checkUnit" show-overflow-tooltip>
                <template slot-scope="scope" v-if="scope.row.checkUnit">{{scope.row.checkUnit | checkUnit}}</template>
            </el-table-column>
            <el-table-column align="center" label="检查人员" prop="checkPerson" show-overflow-tooltip>
                <!-- <template slot-scope="scope" v-if='scope.row.checkUnit'>
                           {{scope.row.checkUnit | checkUnit}}
                </template>-->
            </el-table-column>

            <el-table-column
                    align="center"
                    label="计划检查时间"
                    prop="checkTime"
                    show-overflow-tooltip
                    width="150"
            >
                <template slot-scope="scope" v-if="scope.row.checkTime">{{scope.row.checkTime | formatDate}}</template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="计划结束时间"
                    prop="nextTime"
                    show-overflow-tooltip
                    width="150"
            >
                <template slot-scope="scope" v-if="scope.row.nextTime">{{scope.row.nextTime | formatDate}}</template>
            </el-table-column>

            <el-table-column align="center" label="操作" width="50">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-view btnCss"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                    <!-- <el-tooltip class="item" effect="dark" content="编辑" placement="top-end">
                                 <el-link type="warning" icon="el-icon-edit" @click.stop="editFunction(scope.row)" style="margin:0 20px"></el-link>
                              </el-tooltip>
                              <el-tooltip class="item" effect="dark" content="删除" placement="top-end">
                                 <el-link type="danger" @click.stop="delateFunction(scope.row)" class="el-icon-delete" style="margin:0 20px"><i></i></el-link>
                    </el-tooltip>-->
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-edit
                :editId="editObj.editId"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.type"
                @getList="getList"
                ref="child"
        />
        <!-- 查看 -->
        <see-content
                :editId="seeObj.editId"
                :options="options"
                :show.sync="seeObj.show"
                :title="seeObj.title"
                :type="seeObj.type"
                ref="child"
        />
    </div>
</template>

<script>
    import {apiCheckPlancreatPlanInfo, apiCheckPlandeleteBatch, apiCheckPlanSearch} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";
    import LjPagination from "../../../components/LjPagination";
    import LjSearchFrom from "../../../components/LjSearchForm";
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seecontent";

    let that;
    export default {
        name: "checkPlanPage",
        data() {
            return {
                loading: true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null
                },
                multipleSelection: [],
                //搜索组件数据
                searchObj: null,
                options: [],
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: [
                    {
                        scId: "aaa1112222sss3ddd",
                        crimeTime: "2019-01-20",
                        crimeLocal: "XXX",
                        caseSource: "sssss",
                        HandlingUnit: "aa",
                        investigatorsName: "aa",
                        party: "aa",
                        partyType: "aa",
                        correctionMethod: "aa",
                        correctionResult: "aa",
                        caseStatus: "aa"
                    }
                ],
                objArr: [],
                rowdata: {}
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchFrom,
            seeContent
        },

        methods: {
            //手动生成检查计划数据，并删除当月的其他数据
            onCreate() {
                apiCheckPlancreatPlanInfo().then(res => {
                    this.getList();
                });
            },
            //查询+搜索
            getList() {
                this.loading = true;
                apiCheckPlanSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },
            //新增
            onAdd() {
                this.editObj.type = "2";
                this.editObj.title = "新增监管对象";
                this.editObj.show = true;
                this.editObj.editId = null;
            },
            seeFunction(row) {
                this.seeObj.title = "检查计划查看";
                this.seeObj.show = true;
                this.seeObj.editId = row.planId;
            },

            //编辑
            editFunction(row) {
                this.editObj.title = "修改监管对象信息";
                this.editObj.editId = row.objId;
                this.editObj.show = true;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            //批量删除
            onDelAll() {
                this.$confirm(
                    "此操作将删除" + this.multipleSelection.length + "条数据, 是否继续?",
                    "提示",
                    {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    }
                ).then(() => {
                    let arr = [];
                    this.multipleSelection.forEach(i => {
                        arr.push(i.planId);
                    });
                    apiCheckPlandeleteBatch({planIds: arr.join(",")}).then(() => {
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "批量删除成功!"
                        });
                    });
                });
            },
            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiCheckPlanSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        beforeCreate: function () {
            that = this;
        },

        filters: {
            ifsex(val) {
                return IFSEX[val];
            },
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            },
            objectType(val) {
                if (
                    that.objArr.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.objArr.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            },
            checkUnit(val) {
                if (
                    that.options.find(item => {
                        return item.orgId == val;
                    })
                ) {
                    return that.options.find(item => {
                        return item.orgId == val;
                    }).orgName;
                }
            }
        },
        mounted() {
        },
        created() {
            this.getList();
            apiOrgSearch({keyWord: ""}).then(res => {
                this.options = res.data;
            });

            //检查对象类型
            this.$until.workBookApi("checkObjectType").then(res => {
                this.objArr = res;
            });
        }
    };
</script>

<style scoped>
    .btnCss {
        margin: 0 10px;
    }

    .wrop-search {
        margin-bottom: 10px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
