/**
* 双随机-检查计划组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center width="80%">

            <table class="gridTable" v-loading="loading">
                <tr>
                    <td class="label">对象类型：</td>
                    <td>
                        <span v-if="objArr.find(item=>{return item.dictValue == form.objectType})">{{objArr.find(item=>{return item.dictValue == form.objectType}).dictName}}</span>
                    </td>
                    <td class="label">对象名称：</td>
                    <td>
                        <span>{{form.objectName}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">检查人员：</td>
                    <td>
                        <span>{{form.checkPerson}}</span>
                    </td>
                    <td class="label">检查单位：</td>
                    <td>
                        <span>{{form.checkUnit | checkUnit}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">计划检查时间：</td>
                    <td>
                        <span>{{form.checkTime|formatDate}}</span>
                    </td>
                    <td class="label">计划结束时间：</td>
                    <td>
                        <span>{{form.nextTime|formatDate}}</span>
                    </td>
                </tr>

            </table>


        </el-dialog>
    </div>
</template>
<script>
    import {apiCheckPlanGet} from "../../../api/case";
    import {apiCommonGetUsersByOrg} from "../../../api/system";
    import {formatDate, IFSEX} from "../../../until/wordBook";

    let that;

    export default {
        name: 'edit',
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: 'normal'
            },
            type: {
                type: String,
                default: ''
            },
            options: {
                type: Array,
                default: []
            }
        },
        data() {

            return {
                loading: true,
                checkArr: [
                    {
                        value: 'month',
                        label: '每月检查'
                    },
                    {
                        value: 'quarter',
                        label: '每季度检查'
                    },
                    {
                        value: 'year',
                        label: '每年份检查'
                    }
                ],
                form: {},
                objArr: [],


            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            clearV() {
                this.$refs.ruleForm.clearValidate()
            },

            getUserListByOrg(data) {
                this.form.orgName = this.options.find(i => {
                    return i.orgId === data
                }).orgName;
                apiCommonGetUsersByOrg(data).then(res => {
                    this.userList = res.userList
                })
            },

            //弹框弹出时获取data给form
            initPage() {
                //检查对象类型下拉
                this.$until.workBookApi('checkObjectType').then(res => {
                    this.objArr = res;
                });

                if (this.editId) {
                    // 查看接口调用
                    apiCheckPlanGet(this.editId).then(res => {
                        this.form = res.data;
                        this.loading = false;
                    })
                } else {

                }
            }

        },
        beforeCreate: function () {
            that = this;
        },
        filters: {
            ifsex(val) {
                return IFSEX[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            },
            checkUnit(val) {
                if (that.options.find(item => {
                    return item.orgId == val
                })) {
                    return that.options.find(item => {
                        return item.orgId == val
                    }).orgName
                }
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },

    }
</script>
<style lang='less' scoped>
    table {
        width: 100%;
        border: 1px solid #ccc;

        tr {
            height: 50px;

            td {
                padding-left: 5px;
                border: 1px solid #ccc;
            }
        }
    }


    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;

    }

    .widthselect {
        width: 100%
    }

    .cssfont {
        font-weight: bold;
    }
</style>


