/**
* 双随机-检查计划组件
* @module 系统
* @desc 添加修改
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center top="50px"
                   width="80%">

            <el-form :model="form" :rules="rules" class="demo-ruleForm alert_wrop" label-width="110px" ref="ruleForm"
                     size="medium"
                     status-icon v-loading="loading">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="对象类型：" prop="objectType">
                            <el-select class="widthselect" placeholder="请选择对象类型" v-model="form.objectType">
                                <el-option :key="item.dictValue" :label="item.dictName" :value="item.dictValue"
                                           v-for="item in objArr"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="对象名称：" prop="objectName">
                            <el-input placeholder="请输入对象名称" v-model="form.objectName"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="联系电话：">
                            <el-input placeholder="请输入联系电话" v-model="form.objectTel"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item :rows="4" label="对象地址：" prop="objectAddress" type="textarea">
                            <el-input placeholder="请输入对象地址" v-model="form.objectAddress"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="是否有证：" prop="isCertificates">
                            <el-radio label="1" v-model="form.isCertificates">有证</el-radio>
                            <el-radio label="0" v-model="form.isCertificates">无证</el-radio>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="检查单位：" prop="checkUnit">
                            <el-select class="widthselect" placeholder="请选择检查单位" v-model="form.checkUnit">
                                <el-option :key="item.orgId" :label="item.orgName" :value="item.orgId"
                                           v-for="item in options"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="检查频次：" prop="checkFreq">
                            <el-select class="widthselect" placeholder="请选择对象类型" v-model="form.checkFreq">
                                <el-option :key="item.value" :label="item.label" :value="item.value"
                                           v-for="item in checkArr"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="备注信息：">
                            <el-input :rows="2" placeholder="请输入备注信息" type="textarea" v-model="form.remarks"></el-input>
                        </el-form-item>
                    </el-col>

                </el-row>

            </el-form>
            <span class="dialog-footer" slot="footer">
                <el-button @click="closeDialog" size="small" type="info"><i
                        class="iconfont icon-back"></i>取 消</el-button>
                <el-button @click="submitForm('ruleForm')" size="small" type="primary"><i
                        class="iconfont icon-save"></i>确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiSuperviseObjecAdd, apiSuperviseObjectGet, apiSuperviseObjecUpdate} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";

    export default {
        name: 'edit',
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: 'normal'
            },
            type: {
                type: String,
                default: ''
            },
        },
        data() {

            return {
                loading: true,
                checkArr: [
                    {
                        value: 'month',
                        label: '每月检查'
                    },
                    {
                        value: 'quarter',
                        label: '每季度检查'
                    },
                    {
                        value: 'year',
                        label: '每年份检查'
                    }
                ],
                aaa: '',
                form: {},
                objArr: [],
                options: [],


                rules: {

                    objectType: [
                        {required: true, message: '请选择对象类型', trigger: 'blur'}
                    ],
                    checkUnit: [
                        {required: true, message: '请选择对象类型', trigger: 'blur'}
                    ],
                    objectName: [
                        {required: true, message: '请输入对象名称', trigger: 'blur'},
                        {min: 1, message: '至少1个以上字符', trigger: 'blur'}
                    ],
                    objectTel: [
                        {required: true, message: '请输入联系电话', trigger: 'blur'},
                        {min: 6, message: '至少6个以上数字', trigger: 'blur'}
                    ],
                    objectAddress: [
                        {required: true, message: '请输入对象地址', trigger: 'blur'},
                        {min: 2, message: '至少2个以上字符', trigger: 'blur'}
                    ],
                    checkFreq: [
                        {required: true, message: '请选择检查频次', trigger: 'blur'}
                    ],
                    isCertificates: [
                        {required: true, message: '是否有证件', trigger: 'change'}
                    ],
                    remarks: [
                        {required: true, message: '请输入备注信息', trigger: 'blur'},
                    ],


                }

            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            clearV() {
                this.$refs.ruleForm.clearValidate()
            },
            submitForm(formName) {

                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (!this.editId) {
                            //添加
                            apiSuperviseObjecAdd(this.form).then(res => {
                                this.$emit("getList")
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                });
                                this.closeDialog();
                            })
                        } else {
                            //编辑
                            apiSuperviseObjecUpdate(this.form).then(res => {
                                this.$emit("getList")
                                this.$message({
                                    type: 'success',
                                    message: '修改成功!'
                                });
                                this.closeDialog();
                            })
                        }
                    } else {
                        this.$message({
                            type: 'warning',
                            message: '请填写完整 !'
                        });
                        return false;
                    }
                });
            },
            //检查单位
            remoteMethod() {
                apiOrgSearch({keyWord: ''}).then(res => {
                    this.options = res.data
                })
            },

            //弹框弹出时获取data给form
            initPage() {
                this.remoteMethod();
                //检查对象类型下拉
                this.$until.workBookApi('checkObjectType').then(res => {
                    this.objArr = res;
                });

                if (this.editId) {
                    //    编辑接口

                    apiSuperviseObjectGet(this.editId).then(res => {
                        this.form = res.data;
                        this.form.isCertificates = String(this.form.isCertificates);
                        this.loading = false;
                    })
                } else {
                    this.loading = false;
                    this.form.objectType = '';
                    this.form.objectName = '';
                    this.form.objectTel = '';
                    this.form.objectAddress = '';
                    this.form.isCertificates = '';
                    this.form.checkUnit = '';
                    this.form.checkFreq = '';
                    this.form.remarks = '';
                }
            }

        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    })
                }
            }
        },


    }
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;

    }

    .widthselect {
        width: 100%
    }
</style>


